.main-header {
	position: relative;
	padding-top: 24px;

	&__nav {
		max-height: 0;
		visibility: hidden;
		transition: max-height 0.8s, visibility 1s, padding-bottom 0.8s;

		&.active {
			padding-bottom: 30px;
			max-height: 100vh;
			visibility: visible;
			transition: max-height 0.8s, visibility 1s, padding-bottom 0.8s;

			.main-header__nav-item {
				opacity: 1;
				transform: translateY(0);
				transition: opacity 0.4s, transform 0.4s;
			}
		}

		@media (min-width: 767px) {
			max-height: 100vh;
			visibility: visible;
		}
	}

	&__nav-list {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		list-style: none;
		margin: 0;
		padding-left: 0;

		@media (min-width: 767px) {
			flex-flow: row nowrap;
			justify-content: flex-end;
		}
	}

	&__nav-item {
		opacity: 0;
		transform: translateY(-100%);
		transition: opacity 0.4s, transform 0.4s;

		&:first-child {
			.main-header__nav-link {
				margin-left: 0;
			}
		}

		&:last-child {
			.main-header__nav-link {
				margin-right: 0;
			}
		}

		@media (min-width: 767px) {
			opacity: 1;
			transform: translateY(0);
		}
	}

	&__nav-link {
		position: relative;
		display: block;
		padding: 22px 0 20px;
		font-size: 20px;
		line-height: 1;
		letter-spacing: 0.2px;
		text-decoration: none;
		color: $color-dove-gray;
		transition: color 0.2s;

		&::after {
			content: "";
			position: absolute;
			left: 50%;
			bottom: 0;
			width: 0;
			height: 3px;
			background-color: $color-cornflower-blue;
			transition: left 0.3s, width 0.3s;
		}

		&:hover,
		&:focus {
			border-color: $color-cornflower-blue;
			text-decoration: none;
			color: $color-black;
			transition: color 0.2s, border-color 0.2s;

			&::after {
				left: -5%;
				width: 110%;
				transition: left 0.3s, width 0.3s;
			}
		}

		&.active {
			&::after {
				left: -5%;
				width: 110%;
				transition: left 0.3s, width 0.3s;
			}
		}

		@media (min-width: 767px) {
			margin: 0 20px;
		}
	}
}

.logo {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	padding-bottom: 21px;
	width: fit-content;
	text-decoration: none;
	color: $color-black;
	transition: color 0.2s;

	&__icon {
		display: flex;
		justify-content: center;
		border-radius: 50%;
		width: 44px;
		height: 44px;
		color: $color-white;
		background-color: $color-cornflower-blue;

		span {
			font-family: $font-source-sans;
			font-weight: 400;
			font-size: 64px;
			line-height: 1.1;
			letter-spacing: 0.64px;
			transform: rotate(0);
			transition: transform 0.2s;
		}
	}

	&__text {
		padding-left: 13px;
		font-family: $font-segoe-light;
		font-weight: 300;
		font-size: 42px;
		line-height: 1;
		letter-spacing: 0.42px;
	}

	&:hover,
	&:focus {
		text-decoration: none;
		color: lighten($color-black, 30%);
		transition: color 0.2s;

		.logo__icon span {
			transform: rotate(360deg);
			transition: transform 0.2s;
		}
	}
}

.main-header__burger-btn {
	position: absolute;
	right: 0;
	top: 30px;
	z-index: 10;
	border: none;
	padding: 0;
	min-width: 70px;

	@media (min-width: 767px) {
		display: none;
	}
}

.main-header__burger {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 10;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	margin: auto;
	border-radius: 5px;
	padding: 5px;
	width: 40px;
	height: 30px;
	background-color: transparent;

	> span {
		display: block;
	}

	&.active {
		.main-header__burger-topper {
			width: 28px;
			transform: rotate(135deg) translate(7.5px, -6px);
		}

		.main-header__burger-bottom {
			width: 28px;
			transform: rotate(-135deg) translate(0.5px, -1px);
		}

		.main-header__burger-footer {
			top: 0;
			opacity: 0;
			transform: rotate(180deg);
		}
	}

	@media (min-width: 1025px) {
		display: none;
	}
}

.main-header__burger-topper {
	position: relative;
	border: none;
	border-radius: 40px;
	width: 30px;
	height: 3px;
	background: $color-cornflower-blue;
	transition: transform 0.5s, top 0.2s;
}

.main-header__burger-bottom {
	position: relative;
	border: none;
	border-radius: 40px;
	width: 30px;
	height: 3px;
	background: $color-cornflower-blue;
	transition: transform 0.5s, top 0.2s;
	transition-delay: 0.2s, 0s;
}

.main-header__burger-footer {
	position: relative;
	border: none;
	border-radius: 40px;
	width: 30px;
	height: 3px;
	background: $color-cornflower-blue;
	transition: all 0.5s;
	transition-delay: 0.1s;
}