// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000;
$color-white: #fff;
$color-gray: #909090;
$color-dove-gray: #6c6c6c;
$color-nevada: #6f7173;
$color-iron: #cacdd2;
$color-mercury: #e5e5e5;
$color-cornflower-blue: #5094f2;
$color-valencia: #d94748;
$color-gold-tips: #ebbb11;

// FONTS

$font-default: -apple-system, "BlinkMacSystemFont", "Segoe UI", "Roboto", "Helvetica Neue", "Arial", sans-serif;
$font-segoe: "Segoe WPN", $font-default;
$font-segoe-light: "Segoe WP", $font-default;
$font-source-sans: "Source Sans Pro", $font-default;

$grid-gutter-width: 10px;
$grid-columns: 12;
$grid-breakpoints: (xs: 0, sm: 576px, md: 768px, lg: 992px, xl: 1200px);
$container-max-widths: (sm: 540px, md: 720px, lg: 960px, xl: 980px);
$line-height-base: 1.2;
$spacer: 1.2rem;

$gutter: $grid-gutter-width;
