*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	height: 100%;
	box-sizing: border-box;
	-moz-osx-font-smoothing: grayscale;
	-webkit-font-smoothing: antialiased;
}

body {
	min-height: 100vh;
	font-family: $font-segoe;
}

.wrapper {
	display: flex;
	flex-flow: column nowrap;
	min-height: 100vh;
}
