.main-header {
  position: relative;
  padding-top: 24px; }
  .main-header__nav {
    max-height: 0;
    visibility: hidden;
    transition: max-height 0.8s, visibility 1s, padding-bottom 0.8s; }
    .main-header__nav.active {
      padding-bottom: 30px;
      max-height: 100vh;
      visibility: visible;
      transition: max-height 0.8s, visibility 1s, padding-bottom 0.8s; }
      .main-header__nav.active .main-header__nav-item {
        opacity: 1;
        transform: translateY(0);
        transition: opacity 0.4s, transform 0.4s; }
    @media (min-width: 767px) {
      .main-header__nav {
        max-height: 100vh;
        visibility: visible; } }
  .main-header__nav-list {
    display: flex;
    flex-flow: column nowrap;
    align-items: center;
    list-style: none;
    margin: 0;
    padding-left: 0; }
    @media (min-width: 767px) {
      .main-header__nav-list {
        flex-flow: row nowrap;
        justify-content: flex-end; } }
  .main-header__nav-item {
    opacity: 0;
    transform: translateY(-100%);
    transition: opacity 0.4s, transform 0.4s; }
    .main-header__nav-item:first-child .main-header__nav-link {
      margin-left: 0; }
    .main-header__nav-item:last-child .main-header__nav-link {
      margin-right: 0; }
    @media (min-width: 767px) {
      .main-header__nav-item {
        opacity: 1;
        transform: translateY(0); } }
  .main-header__nav-link {
    position: relative;
    display: block;
    padding: 22px 0 20px;
    font-size: 20px;
    line-height: 1;
    letter-spacing: 0.2px;
    text-decoration: none;
    color: #6c6c6c;
    transition: color 0.2s; }
    .main-header__nav-link::after {
      content: "";
      position: absolute;
      left: 50%;
      bottom: 0;
      width: 0;
      height: 3px;
      background-color: #5094f2;
      transition: left 0.3s, width 0.3s; }
    .main-header__nav-link:hover, .main-header__nav-link:focus {
      border-color: #5094f2;
      text-decoration: none;
      color: #000;
      transition: color 0.2s, border-color 0.2s; }
      .main-header__nav-link:hover::after, .main-header__nav-link:focus::after {
        left: -5%;
        width: 110%;
        transition: left 0.3s, width 0.3s; }
    .main-header__nav-link.active::after {
      left: -5%;
      width: 110%;
      transition: left 0.3s, width 0.3s; }
    @media (min-width: 767px) {
      .main-header__nav-link {
        margin: 0 20px; } }

