.main-header__burger {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 10;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: auto;
  border-radius: 5px;
  padding: 5px;
  width: 40px;
  height: 30px;
  background-color: transparent; }
  .main-header__burger > span {
    display: block; }
  .main-header__burger.active .main-header__burger-topper {
    width: 28px;
    transform: rotate(135deg) translate(7.5px, -6px); }
  .main-header__burger.active .main-header__burger-bottom {
    width: 28px;
    transform: rotate(-135deg) translate(0.5px, -1px); }
  .main-header__burger.active .main-header__burger-footer {
    top: 0;
    opacity: 0;
    transform: rotate(180deg); }
  @media (min-width: 1025px) {
    .main-header__burger {
      display: none; } }

.main-header__burger-topper {
  position: relative;
  border: none;
  border-radius: 40px;
  width: 30px;
  height: 3px;
  background: #5094f2;
  transition: transform 0.5s, top 0.2s; }

.main-header__burger-bottom {
  position: relative;
  border: none;
  border-radius: 40px;
  width: 30px;
  height: 3px;
  background: #5094f2;
  transition: transform 0.5s, top 0.2s;
  transition-delay: 0.2s, 0s; }

.main-header__burger-footer {
  position: relative;
  border: none;
  border-radius: 40px;
  width: 30px;
  height: 3px;
  background: #5094f2;
  transition: all 0.5s;
  transition-delay: 0.1s; }

