.cart {
	padding: 26px 15px 36px;

	&__title {
		margin-bottom: 13px;
		font-size: 42px;
		line-height: 1;
		color: $color-nevada;
	}

	&__picture {
		padding-top: 5px;
		padding-left: 20px;
		width: 100%;
		height: 100%;

		img {
			height: auto;
			max-width: 100%;
		}

		@media (min-width: 1023px) {
			width: 388px;
			height: 275px;
		}
	}

	&__text {
		padding-left: 0;

		h4 {
			margin-bottom: 20px;
			font-size: 26px;
			color: $color-nevada;
		}

		pre {
			max-width: 450px;
			font-family: $font-segoe-light;
			font-weight: 300;
			font-size: 15px;
			line-height: 36px;
			color: $color-gray;
		}

		@media (min-width: 767px) {
			padding-left: 33px;
		}

		@media (min-width: 1023px) {
			padding-left: 63px;
		}
	}

	@media (min-width: 767px) {
		padding: 26px 0 36px;
	}
}
