.advantages {
	position: relative;
	padding-top: 66px;
	padding-bottom: 45px;

	&::before {
		content: "";
		position: absolute;
		left: -2px;
		right: 0;
		top: -6px;
		width: 100%;
		height: 7px;
		background-image: url("../images/sprites/svg/triangle.svg");
		background-repeat: repeat-x;
	}
}

.advantages-item {
	max-width: 250px;
	text-align: center;

	&__pic {
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
		border-radius: 50%;
		padding: 50px 45px;
		width: 186px;
		height: 186px;

		svg {
			width: 100%;
			height: 100%;
		}

		&--blue {
			padding: 40px 50px 40px 58px;
			background-color: $color-cornflower-blue;
		}

		&--red {
			background-color: $color-valencia;

			@media (min-width: 1023px) {
				margin-right: 40px;
			}
		}

		&--yellow {
			background-color: $color-gold-tips;
		}
	}

	&__title {
		margin-bottom: 7px;
		padding-top: 24px;
		font-size: 20px;
		color: $color-cornflower-blue;
	}

	&__text {
		font-size: 14px;
		letter-spacing: 0.01em;
		color: $color-gray;
	}
}
