.main-footer {
	border-top: 1px solid $color-mercury;
	padding-top: 32px;
	padding-bottom: 30px;

	&__copyright {
		font-weight: 300;
		font-size: 15px;
		line-height: 1;
		text-align: center;
		color: $color-gray;

		a {
			font-size: 14px;
			text-decoration: none;
			color: $color-gray;
			transition: color 0.2s;

			&:hover,
			&:focus {
				text-decoration: none;
				color: $color-black;
				transition: color 0.2s;
			}
		}

		@media (min-width: 1200px) {
			padding-right: 75px;
		}
	}
}