.intro-slider {
	padding-top: 40px;
	padding-bottom: 5px;

	&__pic {
		margin-left: 8px;
		padding: 40px;
		width: 345px;
		height: 436px;

		@media (min-width: 767px) {
			padding: 0;
		}
	}

	&__image {
		height: auto;
		max-width: 100%;
	}

	&__text {
		padding-bottom: 10px;
		padding-left: 15px;
		font-family: $font-segoe-light;
		font-weight: 300;
		word-spacing: 0.1em;

		&-title {
			margin-bottom: 7px;
			font-weight: 300;
			font-size: 32 / 320 * 100vw;
			line-height: 1;

			@media (min-width: 767px) {
				font-size: 42px;
			}
		}

		&-p {
			margin-bottom: 24px;
			width: 60%;
			font-weight: 300;
			font-size: 18 / 320 * 100vw;
			line-height: 1.4;

			@media (min-width: 767px) {
				font-size: 26px;
				line-height: 1.8;
			}
		}

		&-btn {
			display: inline-flex;
			align-items: flex-start;
			padding: 9px 9px 9px 11px;
			width: auto;
			font-size: 22px;
			line-height: 1;
			text-decoration: none;
			color: $color-white;
			background-color: $color-cornflower-blue;
			transition: transform 0.2s, box-shadow 0.2s;

			svg {
				margin-right: 11px;
				width: 23px;
				height: 23px;
			}

			&:hover,
			&:focus {
				box-shadow: 3px 3px 8px rgba($color-black, 0.6);
				text-decoration: none;
				color: $color-white;
				transform: scale(1.01);
				transition: transform 0.2s, box-shadow 0.2s;
			}

			@media (min-width: 767px) {
				margin-left: 14px;
			}
		}

		@media (min-width: 767px) {
			padding-left: 75px;
		}
	}

	.carousel-control-prev {
		@media (min-width: 1180px) {
			left: -109px;
		}
	}

	.carousel-control-next {
		@media (min-width: 1180px) {
			right: -109px;
		}
	}

	&__btn {
		top: 48.5%;
		bottom: auto;
		display: inline-block;
		width: auto;
		opacity: 1;

		svg {
			width: 25px;
			height: 63px;
			fill: none;
			stroke: $color-iron;
			stroke-width: 2px;
		}

		&:hover,
		&:focus {
			opacity: 0.8;
		}

		@media (min-width: 1180px) {
			width: 15%;
		}
	}
}
